*{margin:0px; padding:0px;font-family: Operator Mono, arial;transition: all 0.25s;}
body {
 background:#100e17;
 text-align: center;
}
main {
 width:100%; 
}
.loading {
 width:100%;
 display: flex;
 justify-content: center;
 align-items: center;
 flex-direction: column;
 height:99vh; 
 color:#fff
}
.loading img {
 width:100px;
}
.text-warning {
 color:#d8d508;
}
.loading button {
 outline:none !important;
 border-radius:5px;
 padding:6px 14px;
 background:blueviolet;
 color:#fff;
 font-size:14px;
 margin-top:10px;
 cursor: pointer;
}
main > header {
 margin: auto;
 width:100%;  
 display: flex;
 justify-content: space-around;
 align-items: center;
 position: fixed;
 top:0;
 left:0;
 right:0; 
 margin:auto;
 background:#100e17fd;
 z-index: 99 !important;
}
main > header > img {
 height:90px; 
}
main > header > input {
 width:40%; 
 margin:auto;
 padding:10px; 
 border-radius:5px;
 border:none !important;
 outline:none !important;
 border:2.5px solid transparent !important;
 background:#ddd;
}

main > header > input:focus {
 border:2.5px solid #ffffff !important;
}
main > .robo {   
 display: grid;
 grid-template-columns: repeat(auto-fill, minmax(250px, 1fr)); 
 padding:20px;
 grid-gap:20px;
 margin-top:100px;
 background: linear-gradient(170deg, #100e17, #111111, );
}
.robo > div {
 background: #eeeeee;
 color:#222;
 border-radius:6px;
 display: flex;
 flex-direction: column;
 align-items: center;
 padding:5px;
 padding-bottom:25px;
 justify-content: center; 
}
.robo > div:hover {
 transform: scale(1.01);
}
.robo > div > img {
 width:90%; 
 display: flex;
 align-items:center; 
}
.robo > div > p { 
 margin-top:20px;
 margin-bottom:2px;
}
.robo > div > small {
 word-break: break-all;
 text-overflow: ellipsis;
}


@media (max-width:992px) {
 main > .robo {
  margin-top:150px;
 }
 main > header {
  flex-direction: column;
 }
 main > header > input {
  margin-bottom:20px;
 }

}
@media (max-width:767px) {
 main > header > input {
  width:90%;
 }
 main > .robo {    
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); 
  padding:15px;
  grid-gap:15px; 
 }
}

@media (max-width:500px) {
 main > header > img {
  width:90%;
  height:auto;
 }
}